// src/app/categoria/[slug]/page.js
import { getProductsByCategory, getCategoryBySlug } from '@/lib/utils';
import Link from 'next/link';
import Image from 'next/image';
import { notFound } from 'next/navigation';
import ProductCard from '@/components/ProductCard';

export default async function CategoryPage({ params }) {
  const { slug } = params;
  
  // Busca dados assincronamente
  const [category, products] = await Promise.all([
    getCategoryBySlug(slug),
    getProductsByCategory(slug)
  ]);

  // Retorna 404 se categoria não existir
  if (!category) {
    notFound();
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Cabeçalho da Categoria */}
      <div className="mb-10 text-center">
        <h1 className="text-4xl font-extrabold text-gray-900 mb-3 capitalize">
          {category.name.toLowerCase()}
        </h1>
        {category.description && (
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            {category.description}
          </p>
        )}
      </div>

      {/* Listagem de Produtos */}
      {products.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
          {products.map((product) => (
            <ProductCard 
              key={product.id} 
              product={product}
              categorySlug={slug}
            />
          ))}
        </div>
      ) : (
        <div className="text-center py-20">
          <p className="text-gray-500 text-lg">
            Nenhum produto disponível nesta categoria no momento.
          </p>
          <Link 
            href="/" 
            className="mt-4 inline-block px-6 py-2 bg-amber-600 text-white rounded-lg hover:bg-amber-700 transition-colors"
          >
            Voltar para a página inicial
          </Link>
        </div>
      )}
    </div>
  );
}
