// src/components/ProductCard.js
import Link from 'next/link';
import Image from 'next/image';

export default function ProductCard({ product, categorySlug }) {
  return (
    <Link 
      href={`/produto/${product.id}`}
      className="group relative bg-white border border-gray-200 rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-all duration-300"
    >
      {/* Imagem do Produto */}
      <div className="aspect-square bg-gray-100 relative overflow-hidden">
        <Image
          src={`/images/products/${product.image}`}
          alt={product.name}
          fill
          className="object-cover group-hover:scale-105 transition-transform duration-300"
          sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw"
        />
      </div>

      {/* Informações do Produto */}
      <div className="p-4">
        <h3 className="font-medium text-gray-900 text-lg mb-1 line-clamp-2">
          {product.name}
        </h3>
        <div className="flex justify-between items-center mt-3">
          <span className="text-lg font-bold text-gray-900">
            R$ {product.price.toFixed(2).replace('.', ',')}
          </span>
          <span className="inline-block bg-gray-100 text-gray-600 text-xs px-2 py-1 rounded capitalize">
            {categorySlug}
          </span>
        </div>
      </div>
    </Link>
  );
}
